@use "colors";
@use "cams-variables";
@use "mixins";

.application-details-info-block {
  @include mixins.application-details-info-block();

  &__title {
    @include mixins.form-block-title();
  }

  &__text {
    font-size: cams-variables.$text-size-heading-sm;
    font-weight: 700;

    &--grey-70 {
      color: colors.$grey-70;
    }

    &--red-50 {
      color: colors.$red-50;
    }

    &--red-70 {
      color: colors.$red-70;
    }
  }
}
