@use "cams-variables";
@use "colors";

@mixin section-border {
  box-shadow: cams-variables.$box-shadow;
  border-radius: 8px;
}

@mixin sub-section-border {
  border-radius: 8px;
  border: 1px solid colors.$grey-30;
}

@mixin table-border {
  border: 1px solid colors.$grey-20;
}

@mixin sub-section-separator {
  width: 100%;
  border: 1px solid colors.$grey-20;
}

@mixin ellipsis_text {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

@mixin icon-wrapper {
  position: relative;

  &:hover {
    &::before {
      content: "";
      position: absolute;
      top: -4px;
      left: -4px;
      width: 32px;
      height: 32px;
      border-radius: 50%;
      background-color: colors.$grey-20;
    }
  }
}

@mixin form-block-title {
  font-size: cams-variables.$text-size-heading-sm;
  font-weight: 500;
  color: colors.$grey-50;
}

@mixin description-wrapper {
  border: 1px solid colors.$grey-30;
  border-radius: 8px;
  padding: 16px;
}

@mixin application-details-info-block {
  display: flex;
  flex-direction: column;
  gap: 7px;
  width: 100%;
  padding-block-end: 16px;

  &--border {
    border-bottom: 1px solid colors.$grey-20;
  }
}

